*{margin:0px;padding:0px;font-family:sans-serif;}
.main{
    height:100vh;
    width:100vw;
    background:url(images/mountains1.jpg);
    box-sizing:border-box;
    overflow:hidden;
    display:block;
    background-size:100% 100%;
}

.home{
    height:50%;
    width:70%;
    margin:auto;
    margin-top:50px;
    padding:100px;
}

.home>h1{
    text-align:center;
    font-size:2.8em;
    color:black;
}

.home>h1>span{
    color:white;
}

.home>h3{
    text-align:center;
    font-size:1.5em;
    line-height:50px;
    padding-top:20px;
    font-weight:300;
    color:darkslateblue;
    letter-spacing:1.5px;
}

.nav{
    display:flex;
    justify-content:flex-end
}
.nav>ul{
    list-style: none;
    display:flex;
    margin-right: 50px;
}

.nav>ul>li{
    padding:20px 30px;
    margin-right: 20px;
}

.nav>ul>li>a{
    text-decoration: none;
    color: white;
}

.nav>ul>li>a:hover{
    color: lightblue;
}
.about{
    height:100%;
    width:100%;
    padding: 50px;
    box-sizing: border-box;
    justify-content: center;
}

.about>h1{
    color:black;
    font-size:3em;
    font-weight:200;
    letter-spacing:1.5px;
    line-height:100px;
    text-align:center
}
.about>p{
    font-size:1.5em;
    text-align:center;
    letter-spacing:1.5px;
    line-height:40px;
    color:whitesmoke
}

.contact{
    height:100%;
    width:100%;
    margin:auto;
    padding:50px;
}
.contact>h1{
    color:black;
    font-size:3em;
    font-weight:200;
    letter-spacing:1.5px;
    line-height:100px;
    text-align:center;
}

.image{
    height:200px;
    width:200px;
    background:url(images/WhatsApp%20Image%202020-05-20%20at%201.49.32%20AM.jpeg);
    border-radius:50%;
    margin:auto;
    background-size:100% 100%;
    border:1px solid grey;
}
.contact>h2{
    color:white;
    font-size:1.8em;
    font-weight:lighter;
    text-align:center;
    margin-top:30px;
    letter-spacing:1px;
}
.contact>p{
    color:white;
    line-height:60px;
    text-align:center;
    font-size:1.5em;
}
